<template>
  <div class="icon-loading-wrapper" v-show="showIconLoading">
    <div class="loading-wrap">
      <img src="./img/icon-loading.gif">
    </div>

  </div>
</template>
<script>

    export default {
        props: {
          showIconLoading: {
            type: Boolean,
            default: false
          }
        },
        computed: {}
    }

</script>

<style rel="stylesheet/css" lang="scss" scoped>
  .icon-loading-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom:0;
    display: flex;
    justify-content: center;
    align-items: center;
    .loading-wrap{
      height: px2rem(200);
      width: px2rem(200);
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(255, 255, 255, 0.8);
      border-radius: px2rem(5);
    }
    img{
      display: block;
      height: px2rem(180);
      width: px2rem(180);
    }
  }

</style>



