<!--
* 实名认证----刷脸认证
-->
<template>
    <v-page>
        <div slot="header">
            <v-header>
                <v-btn :icon="'icon-back'" @click="jumpBack"></v-btn>
                <h1></h1>
            </v-header>
        </div>
        <div class="face-container">
            <h1 class="title">刷脸认证</h1>
            <p class="txt">
                请确保是
                <span class="name">{{userName}}</span>
                本人操作
            </p>
            <div class="img"></div>
            <p class="tip">平视手机 正对光源</p>
            <p class="lip-tip">唇语验证码为:</p>
            <p class="lip-num">{{lipNum}}</p>
            <div class="btn-wrap">
                <v-linear-button :btnTxt="'开始验证'" @click="checkFace"></v-linear-button>
            </div>
        </div>
        <v-loading :showIconLoading="showIconLoading"></v-loading>
    </v-page>
</template>

<script>
    import {vHeader, vBtn} from '@base/header'
    import {vLinearButton} from '../../component/linearButton'
    import {vLoading} from '../../component/iconLoading'
    export default {
        name: 'certificationFace',
        components: {
            vHeader,
            vBtn,
            vLinearButton,
            vLoading
        },
        data () {
            return {
                // 用户名
                userName: '**胜',
                // 唇語驗證數字
                lipNum: '',
                // 唇语识别错误信息
                lipErrMsg: '',
                showMsgBox: false,
                showIconLoading: false
            }
        },
        methods: {
            checkFace () {
                T.checkFace(this.lipNum, this.showLoading).then((ret) => {
                    this.showIconLoading = false
                    T.go('certification/certificationSucc/index'); //成功
                }).catch((ret) => {
                    this.showIconLoading = false
                    this.showErrMsg(ret.errormsg)
                })
            },
            // 返回
            jumpBack () {
                window.T.back()
            },
            // 獲取唇語數字
            getLipNum () {
                window.T.Native.getVerificationCode().then((data) => {
                    this.lipNum = data.validate_data
                }).catch((err) => {
                    console.log(err)
                })
            },
            // 显示错误信息
            showErrMsg (errMsg) {
                window.T.nativeAlert(errMsg).then((ret) => {
                    this.getLipNum()
                })
            },
            // 开启动画
            showLoading () {
                this.showIconLoading = true
            }
        },
        created () {
            this.getLipNum()
        }
    }
</script>

<style lang="scss" scoped>
    .face-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: #ffffff;
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        min-height: px2rem(1200);
        .title {
            margin-top: px2rem(145);
            margin-bottom: px2rem(28);
            color: #333333;
            font-size: px2rem(52);
            font-family: PingFang-SC-Medium;
            text-align: center;
        }
        .txt {
            font-size: px2rem(28);
            color: $font-light;
            margin-bottom: px2rem(78);
            text-align: center;
            .name {
                color: #F28300;
            }
        }
        .img {
            background: url("../images/bg-face@3x.png") no-repeat center;
            background-size: 100%;
            width: px2rem(560);
            height: px2rem(260);
            margin-bottom: px2rem(73);
        }
        .tip {
            color: #333333;
            font-size: px2rem(40);
            font-family: PingFang-SC-Medium;
            margin-bottom: px2rem(120);
            font-weight: bold;
        }
        .lip-tip{
            color: #333333;
            font-size: px2rem(40);
            margin-bottom: px2rem(20);
        }
        .lip-num{
            text-align: center;
            font-size: px2rem(42);
            color: $font-black;
            margin-bottom: px2rem(60);
        }
        .btn-wrap {
            width: px2rem(670);
            height: px2rem(90);
        }
        .err-msg{
            width: 100%;
            padding: px2rem(20);
            word-break: break-all;
            line-height: px2rem(30);
        }
    }
</style>



